body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  background: #1e5799; /* Old browsers */ /* FF3.6+ */
  background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#1e5799), color-stop(100%,#7db9e8)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(top,  #1e5799 0%,#7db9e8 100%); /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(to bottom,  #1e5799 0%,#7db9e8 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1e5799', endColorstr='#7db9e8',GradientType=0 ); /* IE6-9 */
  min-height:100%;
  }
.capture-button {
  background-color: #86b5e1;
  color: white;
  font-size: 1em;
  border-radius: 18px;
  box-shadow:
    0 0 8px 2px #0ff;
}

.capture-button:hover {
  color: red;
  background-color: lightgreen;
}
.timer-row > p {
  text-shadow:
  0px 0px 5px #0ff;
}
.reset-button, .download-button {
  background-color: #86b5e1;
  color: white;
  font-size: 1em;
  border-radius: 18px;
  box-shadow:
    0 0 8px 2px #0ff;
}

.reset-button:hover, .download-button:hover {
  color: red;
  background-color: lightgreen;
}

.image-button-bar {
  display: flex;
  flex-flow: row;
  justify-content: space-evenly;
  align-content: center;
  max-width: 680px;
  margin: 1% auto 0 auto;
}
.konvaMain {
  margin: auto !important;
  margin-bottom: 0 !important;
}

.konvaMain > .konvajs-content {
  margin: auto !important;
}
.konvaMain > .konvajs-content > canvas {
  border-radius: 4px;
  background-color: #2f2d2e !important;
}
.loading-button {
  background-color: #86b5e1;
  border-radius: 18px;
  color: white;
  margin-top: 1%;
  font-size: 1em;
}

.img-wrapper > img, .konvajs-content > canvas, .konvaMain  {
  max-height: 45vh !important;
  width: 62vh !important;
}

.konvajs-content {
  width: 62vh !important;
}
.imageLabel {
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  font-size: .6em;
}

.imageLabel.top {
  min-height: 22px;
  font-weight: bold;
  background-color: #2c5794;
  text-transform: uppercase;
  color: #f5f5f5;
}

.thumbImage {
  display: block;
  width: 100%;
  height: 100%;
}

@-moz-document url-prefix() {
  .thumbImage {
    margin: 0 !important;
  }
}

.savedImageThumb {
  background-color: #0aa;
  width: 18vw;
  height: 18vw;
  max-height: 100%;
  margin: auto .5% auto .5%;
  border-radius: 8px;
  display: flex;
  cursor: pointer;
  overflow: hidden;
  flex-flow: column;
  overflow: hidden;
}

.selectedImageThumb {
  background-color: #0aa; 
  width: 18vw;
  height: 18vw;
  max-height: 100%;
  margin: auto .5% auto .5%;
  border-radius: 8px;
  display: flex;
  cursor: pointer;
  flex-flow: column;
  color: #f5f5f5;
  overflow: hidden;
}

.selectedImageThumb > .thumbImage {
  margin-top: 25px;
}

.selectedImageThumb > .imageLabel.top {
  color: #2e4057;
  background-color: #0aa; 
}

.selectedImageThumb > .imageGlyph.add,
.selectedImageThumb:hover > .imageGlyph.add {
  color: darkturquoise;
}

.savedImageThumb:hover {
  background-color: #0aa; 
  color: #f5f5f5;
}

.savedImageThumb:hover > .imageLabel.top {
  background-color: #86b5e1;
  color: #2c5794;
}

.savedImageThumb:hover > .imageGlyph {
  color: #99c24d;
}

.savedImageThumb:hover > .imageGlyph.erase {
  color: darkred;
}

.savedImageThumb:hover > .imageLabel.bottom {
  background-color: inherit; 
}

.controlPanel { 
  margin: 1% 0 1% 0;
  padding: 1% 0 1% 0;
  display: flex;
  flex-flow: row nowrap;
  align-content: center;
  justify-content: space-around;
} 

.cpbtn {
  margin: auto;
  color: #f5f5f5;
  border-radius: 3px;
  padding: 1% 2% 1% 2%;
  background-color: darkcyan;
  border-radius: 4px;
}
.cpbtn:hover {
  background-color: darkturquoise;
  color: #2e4057;
  text-decoration: none;
}
.cpbtn:focus {
  background-color: #0aa;
}

.cpbtn.delete:hover {
  color: darkred;
}

.imageGlyph {
  display: flex;
  flex-flow: column;
  align-content: center;
  justify-content: center;
  margin: auto;
  color: #2e4057;
  font-size: 1000%;

}

.cpbtn.delete {
  padding: 1% 2% 1% 2%;
}
.caroCard {
  height: 23vh;
  padding: 0 .5% 1% .5%;
  margin: 0;
  display: flex;
  flex-flow: row nowrap;
  align-content: center;
  justify-content: center;
}

.singlePageCarousel, .multiPageCarousel {
  width: 100%;
}

.carousel-control {
  background-image: none !important;
  color: green;
}

.glyphicon-chevron-left:before {
  color: green;
}

.glyphicon-chevron-right:before { 
  color: green;
}

.carousel-indicators, .singlePageCarousel > .carousel-control {
  display: none;
}

.carousel-indicators > li {
  background-color: #2e4057;
  border-color: #0aa;
}
.carousel-indicators > li.active {
  background-color: cyan;
  border-color: cyan;
}
.titleBanner {
  text-align: center;
  background-color: #2f2d2e;
  color: white;
}

div.stickyHead.row {
  padding: 0;
}

.titleText {
  width: 100%;
  font-size: 1.75em;
  color: red;
  font-weight: bold;
  
  /*position: absolute;
  top: 0;
  left: 0;
  z-index: 999 !important;*/

}

.modelName {
  color: green;
  font-weight: bold;
}

.titleBanner > .particles, 
.titleBanner > .particles > canvas {
  z-index: -99 !important;
  max-height: 8vh !important;
  width: 100% !important;
}

footer {
  color: #2f2d2e;
  text-align: center;
  height: 100%;
  font-size: .75em;
}

.maxMsg {
  width: 100%;
  padding: .5%;
  z-index: 999 !important;
}

.footerMsgBox {
  width: 100%;
  position: absolute;
  bottom: 2.5vh;
  left: 0;
}

.footerLinks {
  margin: 0 .5% 0 .5%;
  display: flex;
  flex-flow: row;
  align-content: center;
  justify-content: space-between;
}

.maxLink.left {
  position: absolute;
  bottom: 0;
  left: 1vw;
  margin-bottom: 1.5vh;
}

.maxLink.right {
  position: absolute;
  bottom: 0;
  right: 1vw;
  margin-bottom: 1.5vh;
}

.maxMsg > a,
.maxLink > a {
  width: 100%;
  text-decoration: underline;
  color: blue;
}

.maxMsg > a:hover,
.maxLink > a:hover {
  color: cyan;
}
.app {
  text-align: center;
}

.app-content {
  height: 100vh;
  display: grid;
  grid-auto-rows: minmax(20px, auto);
  font-size: calc(10px + 2vmin);
  color: white;
  z-index: 100 !important;
}

.image-display, video {
  object-fit: contain;
  margin: 0 !important;
}

.content-card {
  padding: .5%;
  border-radius: 10%;
  background-color: #2c5794; 
  display: inline-block;
}

.content-wrapper {
  width: 100%;
}

.row-one {
  grid-row: 1;
}

.row-two {
  grid-row: 2;
}

.row-three {
  grid-row: 3;
  max-height: 200px;
}

.row-four {
  grid-row: 4;
}

.webcam-display {
  max-height: 45vh;
  width: auto;
}
